import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import { AiOutlineStar } from 'react-icons/ai';
import { Tooltip } from '@mui/material';
import '../assets/css/style.css';

const image_src = {
  ' ': require('../assets/images/yarnovin_city.png'),
  '': require('../assets/images/Yarnovinlogo.png'),
  ' ': require('../assets/images/forum_yarnovin.png'),
  ' ': require('../assets/images/codeyar_web.png'),
  ' ': require('../assets/images/hooshyar_bot.png'),
  ' ': require('../assets/images/codeyar_bot.png')
}

const StarIcon = () => {
  return (
    <Box display='inline-block'>
      <Tooltip title={<b className='vazir_matn'> </b>} placement='top' arrow>
        <IconButton>
          <AiOutlineStar className='AiOutlineStarShadow' size={20} color={"gold"} />
        </IconButton>
      </Tooltip>
    </Box>
  );
}

export default function YarnovinProject(Props) {
  return (
    <>
      <Card style={{ height: '100%' }}>
        <CardMedia
          sx={{ height: 130 }}
          image={image_src[Props.image_src]}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div" fontWeight='bold' display='inline-block' fontFamily='VazirMatnLight'>
            {Props.project_name}{Props.is_popular && true ? <StarIcon /> : ''}{Props.is_incoming && true ? ' () ' : ''}
          </Typography>
          <Typography variant="body2" color="text.secondary" fontFamily='VazirMatnLight'>
            {Props.project_bio}
          </Typography>
        </CardContent>
        <CardActions disableSpacing style={{ display: 'flex', justifyContent: 'flex-end' }}>
          <Button size="large" disabled={Props.is_incoming && true ? true : false}>
            <Typography fontWeight='bold' fontFamily='VazirMatnLight'>
              <Link href={Props.project_src} style={{ textDecoration: 'none' }}>
                
              </Link>
            </Typography>
          </Button>
        </CardActions>
      </Card >
    </>
  );
}